@use "base/variables" as v;
@use "base/mixins" as m;

.galeria {
  @include m.contenedor;

  &-imagenes {
    @include m.grid(1, 2rem);

    @include m.telefono {
      @include m.grid(2, 2rem);
    }
    @include m.tablet {
      @include m.grid(3, 2rem);
    }
    @include m.desktop {
      @include m.grid(4, 2rem);
    }

    img {
      height: 20rem;
      object-fit: cover;
    }
  }
}

.modal {
  position: fixed;
  inset: 0;
  background-color: rgba(v.$negro, 0.5);
  display: grid;
  place-items: center;
  animation: fadeIn ease-in 0.5s;

  &.fade-out {
    animation: fadeOut ease-in fadeIn 0.5s;
  }
  img {
    max-width: 100rem;
  }
}

.modal {
  position: fixed;
  inset: 0;
  background-color: rgba(v.$negro, 0.5);
  display: grid;
  place-items: center;
  animation: fadeIn ease-in 0.5s; // Animación de entrada

  &.fade-out {
    animation: fadeOut ease-in 0.5s; // Animación de salida
  }

  img {
    max-width: 100rem;
  }
}

.btn-cerrar {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: v.$blanco;
  border: none;
  color: v.$negro;
  display: grid;
  place-items: center;
  font-weight: 700;
  cursor: pointer;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
